@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@300;400;500;600&display=swap');

:root {
	--animation-curve: cubic-bezier(1, 0, 0, 1);
}

html{
	background-color: black;
}

#hamburger{
	width:2.667vw;
	height: 0.666vh;
	min-width: 36px;
	min-height: 5px;
	background-color: white;
	position:fixed;
	right: 24px;
	top: 48px;
	cursor: pointer;
}

#hamburger::before, #hamburger::after{
	content: "";
    width: 2.667vw;
    height: 0.666vh;
	min-width: 36px;
	min-height: 5px;
    background-color: white;
    position: absolute;
    display: block;
	transition: all 0.6s var(--animation-curve);
}

#hamburger::before{
	top: -250%;
	left: 0%;
	transform-origin: left top;
	transform:rotateZ(0deg);
}

#hamburger::after{
	bottom: -250%;
	left: 0%;
	transform-origin: left bottom;
	transform:rotateZ(0deg);
}

#toggle:checked ~ #hamburger::before {
	content: "";
	background-color: black;
	transform: rotateZ(41deg);
	left: 10%;
  }
  
#toggle:checked ~ #hamburger::after {
	content: "";
	background-color: black;
	transform: rotateZ(-41deg);
	left: 10%;
  }
  
#toggle:checked ~ #hamburger {
	background-color: black;
	opacity: 0;
  }
  
#toggle ~ #hamburger {
	transition: all 0.5s var(--animation-curve);
  }
  
input {
	display: none;
  }

#content{
	width:100%;
	height:100vh;
	
	scroll-snap-type: y mandatory;
	overflow:auto;
}

#content > section{
	scroll-snap-align: center;
}

section{
	margin-left: 32px;
	margin-right: 32px;
	height: 100vh;
	
}

.my_name{
	color: white;
	font-size: 6.67vw;
	z-index: 1;
	position: relative;
	top: 45vh;
	margin:0;
	overflow-x:hidden;
	white-space: nowrap;
	font-family: 'League Spartan', sans-serif;
	text-shadow: -0.417vw 0.0521vw rgb(105, 105, 105);
}

.my_name:hover{
	cursor: default;
}